'use client';

import { useState } from 'react';
import { RouteConfig, RequestBody, ApiResponse } from '../types/route';
import { FiSend } from 'react-icons/fi';
import { 
  HiOutlineCode,
  HiOutlineChevronDown, 
  HiOutlineChevronRight,
  HiOutlineClipboardCopy 
} from 'react-icons/hi';
import { 
  BiBody, 
  BiData 
} from 'react-icons/bi';
import { MdOutlineApi } from 'react-icons/md';

interface RouteTestProps {
  route: RouteConfig;
}

export default function RouteTest({ route }: RouteTestProps) {
  const [isExpanded, setIsExpanded] = useState(false);
  const [requestBody, setRequestBody] = useState('{\n  "name": "string",\n  "email": "string",\n  "phone": "string"\n}');
  const [pathParams, setPathParams] = useState<Record<string, string>>({});
  const [response, setResponse] = useState<ApiResponse | null>(null);
  const [loading, setLoading] = useState(false);

  const getMethodColor = (method: string) => {
    const colors = {
      GET: 'bg-emerald-500 hover:bg-emerald-600',
      POST: 'bg-blue-500 hover:bg-blue-600',
      PUT: 'bg-amber-500 hover:bg-amber-600',
      DELETE: 'bg-red-500 hover:bg-red-600'
    };
    return colors[method as keyof typeof colors] || 'bg-gray-500';
  };

  const copyToClipboard = (text: string) => {
    navigator.clipboard.writeText(text);
  };

  const handleTest = async () => {
    setLoading(true);
    try {
      let url = route.path;
      Object.entries(pathParams).forEach(([key, value]) => {
        url = url.replace(`{${key}}`, value);
      });
      url = `${process.env.NEXT_PUBLIC_API_URL}${url}`;

      let body: RequestBody = {};
      try {
        body = JSON.parse(requestBody);
      } catch (e) {
        setResponse({
          status: 400,
          data: null,
          error: 'Invalid JSON in request body'
        });
        return;
      }

      const response = await fetch(url, {
        method: route.method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: route.method !== 'GET' ? JSON.stringify(body) : undefined,
      });

      const data = await response.json();
      setResponse({
        status: response.status,
        data,
      });
    } catch (error) {
      setResponse({
        status: 500,
        data: null,
        error: error instanceof Error ? error.message : 'Unknown error occurred'
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden transition-all duration-200 hover:shadow-md">
      <div 
        className="flex items-center p-4 cursor-pointer bg-gray-50 hover:bg-gray-100 transition-colors"
        onClick={() => setIsExpanded(!isExpanded)}
      >
        <div className={`px-3 py-1.5 rounded-md text-white font-medium flex items-center gap-2 ${getMethodColor(route.method)}`}>
          <MdOutlineApi className="text-lg" />
          {route.method}
        </div>
        <span className="ml-4 font-mono text-sm text-gray-600">{route.path}</span>
        <span className="ml-4 text-gray-500 text-sm">{route.description}</span>
        <div className="ml-auto text-gray-400">
          {isExpanded ? <HiOutlineChevronDown className="text-xl" /> : <HiOutlineChevronRight className="text-xl" />}
        </div>
      </div>

      {isExpanded && (
        <div className="p-6 space-y-6">
          {/* Parameters Section */}
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-2 text-gray-700">
                <BiData className="text-xl" />
                <h3 className="font-medium">Parameters</h3>
              </div>
              <button
                onClick={() => copyToClipboard(JSON.stringify({ params: pathParams, body: requestBody }, null, 2))}
                className="text-gray-400 hover:text-gray-600 transition-colors"
              >
                <HiOutlineClipboardCopy className="text-xl" />
              </button>
            </div>
            {route.path.includes('{') && (
              <div className="grid gap-4 p-4 bg-gray-50 rounded-lg">
                {route.path.split('/').map(part => {
                  if (part.startsWith('{') && part.endsWith('}')) {
                    const param = part.slice(1, -1);
                    return (
                      <div key={param} className="space-y-1">
                        <label className="text-sm font-medium text-gray-700 flex items-center gap-1">
                          {param}
                          <span className="text-red-500">*</span>
                        </label>
                        <input
                          type="text"
                          className="w-full px-3 py-2 rounded-md border border-gray-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all"
                          value={pathParams[param] || ''}
                          onChange={(e) => setPathParams({
                            ...pathParams,
                            [param]: e.target.value
                          })}
                          placeholder={`Enter ${param}`}
                        />
                      </div>
                    );
                  }
                  return null;
                })}
              </div>
            )}
          </div>

          {/* Request Body Section */}
          {route.method !== 'GET' && (
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2 text-gray-700">
                  <BiBody className="text-xl" />
                  <h3 className="font-medium">Request Body</h3>
                </div>
                <div className="flex items-center gap-2 text-sm text-gray-500">
                  <HiOutlineCode className="text-lg" />
                  application/json
                </div>
              </div>
              <div className="relative">
                <textarea
                  className="w-full h-48 p-4 font-mono text-sm bg-gray-50 rounded-lg border border-gray-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all"
                  value={requestBody}
                  onChange={(e) => setRequestBody(e.target.value)}
                />
                <button
                  onClick={() => copyToClipboard(requestBody)}
                  className="absolute top-2 right-2 text-gray-400 hover:text-gray-600 transition-colors"
                >
                  <HiOutlineClipboardCopy className="text-xl" />
                </button>
              </div>
            </div>
          )}

          {/* Execute Button */}
          <div className="flex justify-end">
            <button
              onClick={handleTest}
              disabled={loading}
              className="flex items-center gap-2 px-6 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
            >
              <FiSend className={`text-lg ${loading ? 'animate-pulse' : ''}`} />
              {loading ? 'Sending...' : 'Execute'}
            </button>
          </div>

          {/* Response Section */}
          {response && (
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2 text-gray-700">
                  <BiData className="text-xl" />
                  <h3 className="font-medium">Response</h3>
                </div>
                <div className="flex items-center gap-2">
                  <span className="text-sm text-gray-500">Status:</span>
                  <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                    response.status >= 400 ? 'bg-red-100 text-red-700' : 'bg-green-100 text-green-700'
                  }`}>
                    {response.status}
                  </span>
                </div>
              </div>
              <div className="relative">
                <pre className="p-4 bg-gray-50 rounded-lg border border-gray-200 overflow-auto font-mono text-sm">
                  {JSON.stringify(response.data, null, 2)}
                </pre>
                <button
                  onClick={() => copyToClipboard(JSON.stringify(response.data, null, 2))}
                  className="absolute top-2 right-2 text-gray-400 hover:text-gray-600 transition-colors"
                >
                  <HiOutlineClipboardCopy className="text-xl" />
                </button>
              </div>
              {response.error && (
                <div className="p-4 bg-red-50 text-red-700 rounded-lg text-sm">
                  {response.error}
                </div>
              )}
            </div>
          )}
        </div>
      )}
    </div>
  );
} 